/// <reference lib="webworker" />

import { Stint } from "../../stint/model/stint";
import { RaceConfig } from "../../race/model/race-config";
import { addMinutes, isAfter, isBefore, subMinutes } from "date-fns";
import { PitLaneConfig } from "../model/pit-lane-config";

addEventListener('message', ({ data }) => {
  const willEndRaceDate = data.willEndRaceDate as Date | undefined;
  const activeStint = data.activeStint as Stint | undefined;
  const raceConfig = data.raceConfig as RaceConfig;
  const pitLaneConfig = data.pitLaneConfig as PitLaneConfig;

  const minStintMinute = raceConfig.minStintMinute;
  const pitLaneCloseBeforeEndRaceMinute = pitLaneConfig.closeBeforeEndRaceMinute;

  let open = false;
  let openInMilliseconds = 0;

  if (willEndRaceDate && activeStint) {
    open = calculatePitLaneOpen(willEndRaceDate, activeStint, minStintMinute, pitLaneCloseBeforeEndRaceMinute);
    openInMilliseconds = calculatePitLaneOpenInMilliseconds(activeStint, minStintMinute);
  }

  postMessage({
    open: open,
    openInMilliseconds: openInMilliseconds,
  });
});

function calculatePitLaneOpenInMilliseconds(activeStint: Stint, minStintMinute: number): number {
  const now = new Date().getTime();
  return addMinutes(activeStint.startedAt, minStintMinute).getTime() - now;
}

function calculatePitLaneOpen(
    endRaceDate: Date,
    activeStint: Stint,
    minStintMinute: number,
    pitLaneCloseBeforeEndRaceMinute: number,
): boolean {
  const now = new Date();

  // Check if the current time is at least X minutes before the end of the race
  const pitLaneCloseTime = subMinutes(endRaceDate, pitLaneCloseBeforeEndRaceMinute);
  const isBeforePitLaneClose = isBefore(now, pitLaneCloseTime);

  // Check if at least X minutes have passed since the start of the active active-stint
  const minStintTime = addMinutes(activeStint.startedAt, minStintMinute);
  const isAfterMinStintTime = isAfter(now, minStintTime)

  return isBeforePitLaneClose && isAfterMinStintTime;
}
